package fry.lab.app.root

import com.arkivanov.decompose.router.stack.ChildStack
import com.arkivanov.decompose.value.Value
import fry.lab.app.mainScreen.FirstScreenComponent
import fry.lab.app.settingsScreen.SecondScreenComponent

internal interface RootComponent {

    val stack: Value<ChildStack<*, Child>>

    fun onBackClicked(toIndex: Int)

    sealed class Child {
        class Screen1(val component: FirstScreenComponent) : Child()
        class Screen2(val component: SecondScreenComponent) : Child()
    }
}