package fry.lab.app.views

import androidx.compose.foundation.layout.Row
import androidx.compose.foundation.layout.Spacer
import androidx.compose.foundation.layout.size
import androidx.compose.foundation.layout.width
import androidx.compose.material3.Icon
import androidx.compose.material3.Text
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.text.font.FontWeight
import androidx.compose.ui.unit.dp
import androidx.compose.ui.unit.sp
import fry_lab_app.composeapp.generated.resources.Res
import fry_lab_app.composeapp.generated.resources.main_info_circle
import org.jetbrains.compose.resources.DrawableResource
import org.jetbrains.compose.resources.painterResource

@Composable
fun TextWithImgHint(
    modifier: Modifier = Modifier,
    text: String,
    leftIconRes: DrawableResource,
    hintText: String,
) = Row(
    modifier = modifier,
    verticalAlignment = Alignment.CenterVertically
) {
    Icon(
        modifier = Modifier.size(18.dp),
        painter = painterResource(leftIconRes),
        contentDescription = null,
        tint = Color.Black
    )
    Spacer(Modifier.width(8.dp))
    Text(
        text = text,
        fontSize = 12.sp,
        fontWeight = FontWeight.Bold
    )
    Spacer(Modifier.width(8.dp))
    Icon(
        modifier = Modifier.size(18.dp),
        painter = painterResource(Res.drawable.main_info_circle),
        contentDescription = null,
        tint = Color(0xFFA3B8EB)
    )
}