import org.jetbrains.compose.resources.DrawableResource

@Composable
fun DropdownButton(
    modifier: Modifier = Modifier,
    title: String,
    initDropDownValue: String = "- - - - -",
    dropDownValues: ImmutableList<String>,
    onValueChanged: (String) -> Unit,
    leftIconRes: DrawableResource,
) {
    var expanded by remember { mutableStateOf(false) }
    var localValue by remember { mutableStateOf(initDropDownValue) }
    val interactionSource = remember { MutableInteractionSource() }

    Column(modifier) {
        TextWithImgHint(text = title, hintText = "", leftIconRes = leftIconRes)
        Spacer(Modifier.height(4.dp))
        Box(modifier = Modifier.width(400.dp).height(50.dp)) {
            Box(
                modifier = Modifier
                    .fillMaxSize()
                    .background(color = Color.Transparent)
                    .border(width = 1.dp, color = Color.Gray, shape = RoundedCornerShape(8.dp))
                    .padding(horizontal = 16.dp)
                    .clickable(
                        interactionSource = interactionSource,
                        indication = null,
                        onClick = { expanded = true }
                    )
            ) {
                Text(
                    modifier = Modifier.align(Alignment.CenterStart),
                    text = localValue,
                    color = Color.Black,
                    fontSize = 12.sp
                )
                Icon(
                    modifier = Modifier.align(Alignment.CenterEnd),
                    imageVector = Icons.Default.ArrowDropDown,
                    contentDescription = null,
                    tint = Color.Black
                )
            }

            DropdownMenu(
                modifier = Modifier
                    .crop(vertical = 8.dp)
                    .clip(RoundedCornerShape(8.dp))
                    .width(300.dp),
                expanded = expanded,
                onDismissRequest = { expanded = false },
                containerColor = Color(0xFF64666D),
            ) {
                dropDownValues.map {
                    CustomDropDownItem(
                        title = it,
                        onclick = {
                            localValue = it
                            onValueChanged(it)
                            expanded = false
                        }
                    )
                }
            }
        }
        Spacer(modifier = Modifier.height(16.dp))
    }
}

@Composable
private fun CustomDropDownItem(
    title: String,
    onclick: () -> Unit
) = DropdownMenuItem(
    text = {
        Text(
            text = title,
            color = Color.White,
            fontSize = 12.sp
        )
    },
    onClick = onclick
)

private fun Modifier.crop(
    horizontal: Dp = 0.dp,
    vertical: Dp = 0.dp,
): Modifier = this.layout { measurable, constraints ->
    val placeable = measurable.measure(constraints)
    fun Dp.toPxInt(): Int = this.toPx().toInt()
    layout(
        placeable.width - (horizontal * 2).toPxInt(),
        placeable.height - (vertical * 2).toPxInt()
    ) {
        placeable.placeRelative(-horizontal.toPx().toInt(), -vertical.toPx().toInt())
    }
}